export function productMediaListUtils({ element }) {
    const { settings } = element;
    const slideDesktop = settings.$itemsDisplayed.desktop?.value;
    const slideTablet = settings.$itemsDisplayed.tablet?.value ?? settings.$itemsDisplayed.desktop?.value;
    const slideMobile = settings.$itemsDisplayed.mobile?.value ?? settings.$itemsDisplayed.desktop?.value;
    const gapDesktop = settings.$itemsSpacing.desktop?.value;
    const gapTablet = settings.$itemsSpacing.tablet?.value ?? settings.$itemsSpacing.desktop?.value;
    const gapMobile = settings.$itemsSpacing.mobile?.value ?? settings.$itemsSpacing.desktop?.value;
    return {
        slideDesktop,
        slideTablet,
        slideMobile,
        gapDesktop,
        gapTablet,
        gapMobile,
    };
}
